html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__logo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: flex;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__name {
  margin-top: 4px;
}

.card__product-code {
  color: #616070;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 24px;
  display: flex;
}

.card__priceText {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__priceAmount {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  box-sizing: border-box;
  cursor: pointer;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__ranking {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 16px;
  display: flex;
}

.card__review {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

